html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  padding: 0 2rem;
}

a {
  text-decoration: none;
  color: black;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.dark {
  background-color: black;
}

.none {
  display: none !important;
}

.m-10 {
  margin: 1rem;
}

.input-sm {
  width: 15rem;
}

.text-right {
  text-align: right;
}

img {
  width: 3rem;
}

.desactivado {
  display: none;
}

.swal2-popup {
  font-size: 2rem !important;
}

.seleccionado {
  background-color: #00eeff !important;
}

.subSeleccionado {
  background-color: #2f7881;
}

.botones {
  padding: 2rem 0;
  display: flex;
  justify-content: space-around;
  background-color: lightgreen;
}
.botones button {
  border: none;
  border-radius: 1.5rem;
  padding: 1rem 2rem;
  font-size: 2rem;
}
.botones button:hover {
  cursor: pointer;
  transform: scale(1.2);
  background-color: gray;
  color: white;
}

.alerta {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.alerta .texto {
  color: white;
}
.alerta .texto p {
  font-size: 2rem;
}

.spinner {
  margin-left: 5rem;
  border: 0.4rem solid black;
  width: 3.6rem;
  height: 3.6rem;
  border-radius: 50%;
  border-left-color: transparent;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
