.emitir {
  padding: 0;
}

.tdEnd {
  text-align: end;
}

.emitirComprobante {
  width: 100%;
  background-color: gray;
}
.emitirComprobante .subseleccionado {
  background-color: #2f7881;
}
.emitirComprobante.mostrarNegro {
  background-color: black;
}
.emitirComprobante .parteAfip {
  margin-top: 1.5rem;
}
.emitirComprobante .buscarAfip {
  background-color: lightgrey;
  padding: 1.5rem;
  border-radius: 7.5rem;
}
.emitirComprobante .buscarAfip:hover {
  cursor: pointer;
}
.emitirComprobante .formulario-verde {
  background-color: lightgreen;
}
.emitirComprobante .formulario-negro {
  background-color: black;
  color: white;
}
.emitirComprobante .formulario-negro button {
  color: white;
}
.emitirComprobante .formularioFactura #codigo, .emitirComprobante .formularioFactura #precio, .emitirComprobante .formularioFactura #descripcion {
  padding: 1rem;
}
.emitirComprobante .formularioFactura #codigoC {
  width: 5rem;
}
.emitirComprobante .formularioFactura .buscar {
  display: flex;
}
.emitirComprobante .formularioFactura .buscar .nombre {
  margin-left: 1rem;
}
.emitirComprobante .formularioFactura .arriba {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 1rem;
}
.emitirComprobante .formularioFactura .medio {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10rem;
}
.emitirComprobante .formularioFactura .abajo {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10rem;
}
.emitirComprobante .formularioFactura .formulario__campo {
  margin-bottom: 0.5rem;
}
.emitirComprobante .formularioFactura .formulario__campo #nombre {
  width: 35rem;
}
.emitirComprobante .formularioFactura .formulario__campo input {
  width: 90%;
}
.emitirComprobante .formularioFactura .select {
  display: flex;
  flex-direction: column;
}
.emitirComprobante .formularioFactura .parteBuscar {
  display: grid;
  grid-template-columns: 3fr 1fr;
}
.emitirComprobante .formularioFactura .parteBuscar .parte-producto {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.emitirComprobante .formularioFactura .parteBuscar .parte-producto div {
  display: flex;
  flex-direction: column;
}
.emitirComprobante .formularioFactura .parteBuscar .parte-producto .parte-producto_codigo, .emitirComprobante .formularioFactura .parteBuscar .parte-producto .parte-producto_precio {
  width: 10%;
}
.emitirComprobante .formularioFactura .parteBuscar .parte-producto .parte-producto_descripcion {
  width: 60%;
}
.emitirComprobante .formularioFactura .parteBuscar .empresa {
  width: 50%;
  margin-left: 10rem;
  display: flex;
  flex-direction: column;
}
.emitirComprobante .formularioFactura .parteBuscar .empresa label {
  text-align: start;
  font-size: 2rem;
}
.emitirComprobante .formularioFactura .parteBuscar .empresa select {
  padding: 1rem;
}
.emitirComprobante .table {
  background-color: white;
  width: 100%;
  border-collapse: collapse;
}
.emitirComprobante .table thead tr {
  background-color: #ffdfdf;
}
.emitirComprobante .table thead tr td {
  border: 0.1rem solid black;
  font-size: 2rem;
}
.emitirComprobante .table tbody tr td {
  font-size: 2rem;
  margin: 0;
  border: 0.1rem solid black;
}
.emitirComprobante .table tbody tr:hover {
  cursor: pointer;
}
.emitirComprobante .table tbody tr .totalP, .emitirComprobante .table tbody tr .precioU {
  text-align: end;
}
.emitirComprobante .table .enNegro {
  color: black;
}
.emitirComprobante .total {
  display: flex;
  padding-top: 1rem;
}
.emitirComprobante .total .descuento {
  margin-left: auto;
}
.emitirComprobante .total .total__input {
  margin-left: auto;
  display: flex;
  justify-content: flex-end;
}
.emitirComprobante .total .usuario p {
  font-size: 1rem;
  margin: 0;
  padding: 1rem;
  border: 1px solid #000;
}
.emitirComprobante .total #total {
  font-weight: 900;
  font-size: 2.4rem;
  color: #000;
  background-color: white;
  width: 70%;
}
.emitirComprobante .tipoVenta {
  display: flex;
  justify-content: center;
}
.emitirComprobante .tipoVenta .form-check {
  margin-right: 2rem;
}
.emitirComprobante label {
  font-size: 2rem;
  font-weight: 900;
}
.emitirComprobante .listaProductos {
  background-color: white;
  overflow: scroll;
  height: 30rem;
  border: 0.1rem solid black;
}
@media (min-height: 750px) {
  .emitirComprobante .listaProductos {
    height: 38rem;
  }
}
@media (min-height: 800px) {
  .emitirComprobante .listaProductos {
    height: 42rem;
  }
}
@media (min-height: 900px) {
  .emitirComprobante .listaProductos {
    height: 50rem;
  }
}
@media (min-height: 950px) {
  .emitirComprobante .listaProductos {
    height: 55rem;
  }
}
.emitirComprobante .opcional {
  display: flex;
  border: 0.1rem solid black;
}
.emitirComprobante .opcional input {
  transform: scale(1) !important;
}
.emitirComprobante .partefinal {
  display: flex;
  justify-content: space-between;
}
.emitirComprobante .partefinal .cobrado input {
  padding: 1rem;
}
.emitirComprobante .partefinal .numeroFactura {
  margin-left: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1rem;
}
.emitirComprobante .partefinal .seccion-enviar {
  margin-top: 2rem;
  display: flex;
  align-items: center;
}
.emitirComprobante .partefinal .seccion-enviar .seccion-enviar-boton {
  margin-left: 5rem;
  background-color: transparent;
  border: none;
}
.emitirComprobante .partefinal .seccion-enviar .seccion-enviar-boton p {
  margin: 0;
}
.emitirComprobante .partefinal .seccion-enviar .seccion-enviar-boton .seccion-enviar-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  margin-right: 2rem;
}
.emitirComprobante .partefinal .seccion-enviar .seccion-enviar-boton:hover {
  cursor: pointer;
  background-color: #44ce44;
}
@media print {
  .emitirComprobante {
    display: none;
  }
}

.tipoVenta .form-check input {
  transform: scale(1.8);
  margin-right: 0.5rem;
}

.emitirNota .notaCredito {
  background-color: lightgreen;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.emitirNota .notaCredito .none {
  display: none !important;
}
.emitirNota .notaCredito .formulario__campo {
  display: flex;
  flex-direction: column;
}
.emitirNota .notaCredito .formulario__campo input, .emitirNota .notaCredito .formulario__campo select {
  width: 90%;
}
.emitirNota .notaCredito .arriba {
  display: grid;
