}
.emitirNota .notaCredito .listaProductos .table tbody td {
  border: 0.1rem solid black;
}
.emitirNota .notaCredito .total {
  padding: 0.5rem 0;
  display: grid;
  width: 100%;
  grid-template-columns: 2fr 1fr 1fr;
}
.emitirNota .notaCredito .total input {
  padding: 0.5rem;
  font-size: 1.5rem;
}
.emitirNota .notaCredito .total .total__input {
  display: flex;
  justify-content: flex-end;
}
.emitirNota .notaCredito .total #descuento {
  width: 15%;
}
.emitirNota .notaCredito .radioYtotal {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  width: 100%;
}
.emitirNota .notaCredito .radioYtotal .vendedor {
  text-align: center;
  border: 0.1rem solid black;
  width: 50%;
}
.emitirNota .notaCredito .radioYtotal .radios {
  border: 0.1rem solid black;
  font-size: 2rem;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 1rem;
}
.emitirNota .notaCredito .radioYtotal .radios .radio {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.emitirNota .notaCredito .radioYtotal .radios .radio input {
  width: 100%;
  height: 1em;
}
.emitirNota .notaCredito .radioYtotal .cobrado {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem;
  font-size: 2rem;
}
.emitirNota .notaCredito .botones {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-bottom: 1rem;
  padding-top: 2rem;
}
.emitirNota .notaCredito .botones button {
  background-color: lightgreen;
  border: none;
}
.emitirNota .notaCredito .botones button:hover {
  transform: scale(1.2);
  cursor: pointer;
}
.emitirNota .notaCredito .botones button p {
  margin: 0;
}